import "../App.css";

export default function About() {
  return (
    <section class="section is-fullheight is-fullheight-with-navbar" id="about">
      <div class="container">
        <div class="section-heading">
          <h1 class="title is-2">About Me</h1>
          <h2 class="subtitle is-4">
            <div class="columns">
              <div class="column">
                <figure class="image">
                  <img alt="" src={process.env.PUBLIC_URL + "/head.jpg"} />
                </figure>
              </div>
              <div class="column">
                <p>
                My name is Krijan Prajapati. I am a final year Software Engineering student with sub-major in Data Analytics. I have worked effectively for various associations and acquired technical abilities and soft skills. I am strongly interested in using data analytics to investigate and tackle complex business issues. My expertise is arranging data in a meaningful manner, organizing, and forecasting trends. I am currently working for Commonwealth Bank of Australia as Machine Learning Engineer and using cloud technology for the work process.
                </p>
                <br/>
                <a
                  class="button is-dark"
                  href={
                    process.env.PUBLIC_URL + "/Krijan_Resume.pdf"
                  }
                  target="_blank"
                  rel="noreferrer noopener"
                >
                  View Resume
                </a>
              </div>
            </div>
          </h2>
        </div>
      </div>
    </section>
  );
}
